@import 'fonts';
@import 'layout';

#varioMain {
	font-size: calc(100vw / 120);

	@media ( max-width: 769px ) {
		font-size: calc(100vw / 48);
		width: -zf-bp-to-em(769px);
		overflow-x: hidden;
	}

	.main-font {
		font-family: ProximaNova-Regular;
		font-style: normal;
		font-weight: normal;
	}

	.container {
		position: relative;
		height: 100%;

		@media ( max-width: 769px ) {
			width: -zf-bp-to-em(769px);
