	.logo-section {
		position: relative;
		background: url(../img/block-1.png);
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		height: -zf-bp-to-em(1555px);
		width: -zf-bp-to-em(1895px);

		@media ( max-width: 769px ) {
			background: url(../img/banner.png);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			height: -zf-bp-to-em(1304px);
			width: -zf-bp-to-em(769px);
		}
	}

	.logo-text {
		position: absolute;
		// top: -zf-bp-to-em(1076.85px);
		top: 61em;
		left: -zf-bp-to-em(448px);
		width: -zf-bp-to-em(1000px);
		display: flex;
		flex-direction: column;
		justify-content: center;
		opacity: 0;
		transition: 0.6s linear;

		p {
			font-weight: bold;
			font-size: -zf-bp-to-em(40px);
			line-height: -zf-bp-to-em(50px);
			text-align: center;
			text-transform: uppercase;
			color: #fff;
		}

		span {
			font-weight: normal;
			font-size: -zf-bp-to-em(32px);
			text-align: center;
			text-transform: uppercase;
			color: #fff;
		}

		@media ( max-width: 769px ) {
			top: 48em;
			left: 0;
			width: -zf-bp-to-em(769px);

			p { font-size: -zf-bp-to-em(32px); }
			span { font-size: -zf-bp-to-em(28px); }
		}
