		@media ( max-width: 769px ) {
			width: -zf-bp-to-em(595.02px);
			// height: -zf-bp-to-em(131.68px);  //8.23em
			left: 5em;
			top: 43em;
		}
	}

	.logo-disabled {
		display: none;
	}

	.images-section {
		position: relative;
		background: #fff;
		height: -zf-bp-to-em(719.95px);

		@media ( max-width: 769px ) {
			height: -zf-bp-to-em(287.98px);
		}


		.img-container {
			display: flex;
			flex-flow: row wrap;
			height: -zf-bp-to-em(719.95px);
			width: 100%;

			@media ( max-width: 769px ) {
