			@media ( max-width: 769px ) {
				// top: -zf-bp-to-em(60px);
				top: 0;
				left: -zf-bp-to-em(38.5px);
			}
		}

		.constructor-container {
			position: absolute;
			top: -zf-bp-to-em(175.04px);
			width: 100%;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			flex-wrap: wrap;

			span {
				font-size: -zf-bp-to-em(48px);
				font-weight: bold;
				padding-top: 5em;
				color: #8a8a8a;
			}

			@media ( max-width: 769px ) {
				span { order: 1; }
				span:last-of-type {
					order: 3;
					width: 2em;
					padding-top: 3.75em;
					padding-bottom: .25em;
					padding-left: 7.75em;
				 }
			}
		}

		.cont-names {
			height: -zf-bp-to-em(49.17px);

			p {
				align-self: center;
				font-size: -zf-bp-to-em(24px);
				line-height: -zf-bp-to-em(29px);
				text-align: center;
				text-transform: uppercase;
				color: #000000;
				margin-top: .25em;
			}

			@media ( max-width: 769px ) {
				order: 2;
			}
		}

		.cont-names.main-cream {
			background: linear-gradient(99.27deg, #f6eef1 -0.91%, #fae7ee 100%);

			@media ( max-width: 769px ) {
				margin-left: 3em;
				order: -1;
			}
		}

		.cont-names.elyx {
			background: linear-gradient(100.95deg, #e5f5fd -0.69%, #f1edcc 132.92%);

			@media ( max-width: 769px ) {
				order: 4;
			}
		}

		.cont-names.second-cream {
			background: #e8f7fa;
			@media ( max-width: 769px ) {
				margin-right: 3em;
			}
		}

		.cont-items {
			display: flex;
			flex-direction: row;
		}

		@mixin fabz($num) {
			background: url(../img/fab/fab-#{$num}.png);
			background-size: cover;
			background-position: center;
			width: -zf-bp-to-em(260px);
			height: -zf-bp-to-em(426px);
			margin-top: -zf-bp-to-em(15.35px);
			transition: 0.2s linear;

			&:hover {
				background: url(../img/fab/hfab-#{$num}.png);
				background-size: cover;
				background-position: center;
				width: -zf-bp-to-em(260px);
				height: -zf-bp-to-em(426px);


				.fab-hidden {
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-content: center;
					align-items: center;
				}
			}

			@media ( max-width: 769px ) {
				width: -zf-bp-to-em(193.13px);
				height: -zf-bp-to-em(386.95px);

				&:hover {
					background: url(../img/fab/fab-#{$num}.png);
					background-size: cover;
					background-position: center;
					width: -zf-bp-to-em(193.13px);
					height: -zf-bp-to-em(386.95px);

					.fab-hidden {
						display: none;
					}
				}
			}
		}

		@for $i from 1 through 8 {
			.fab-#{$i} {
				@include fabz($i);
			}
		}

		.fab-1, .fab-2, .fab-7 {
			.fab-hidden {
				p { color: #000; }
				span { color: #000; }
			}
		}

		.fab-hidden {
			display: none;
			padding-top: -zf-bp-to-em(120.33px);

			p {
				padding: 0;
				font-weight: bold;
				font-size: -zf-bp-to-em(24px);
				line-height: 100%;
				text-align: center;
				color: #fff;
				text-transform: uppercase;
				text-shadow: 0px 4px 20px rgba(0, 109, 159, 0.5);
			}

			span {
				padding-top: 1em;
				width: -zf-bp-to-em(180px);
				font-weight: 600;
				font-size: -zf-bp-to-em(20px);
				line-height: 120%;
				text-align: center;
				color: #fff;
				text-shadow: 0px 4px 20px rgba(0, 109, 159, 0.5);
			}
		}

